.text-with-icon {
  position: relative;
  display: inline-flex;
  vertical-align: middle;
  align-self: center;

  .icon {
    position: relative;
    bottom: -0.3em;
    width: 1em;
    height: 1em;
    fill: currentColor;
    &.icon-left {
      margin-right: .3em;
      order: -1;
    }
    &.icon-right {
      margin-left: .3em;
      order: 999;
    }
  }
}
